body {
  background: #123;
  color: #FED;
  padding: 0;
  margin: 0;
}

main {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-flow: column;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  p { text-align: center;}
  background-size: cover;
  min-height: 100vh;

  .hover-title {
    position: absolute;
    z-index: 100;
    width: 100%;
    text-align: center;
  }

  .logo {
    width: 200px;
    height: 150px;
    margin: 20px;
    z-index: 10;
  }

  .welcome-message {
    width: 22em;
    font-size: .9em;
    color: rgb(92, 167, 238);
    padding: 2em;
    align-self: flex-start;
    position: relative;

    p { width: 100%; text-align: left;}
  }
}

.debugger {
  position: absolute;
  display: flex;
  justify-items: center;
  align-items: center;
  align-content: center;
  top: 0;
  left: 0;
  width: auto;
  height: 3em;
  background: #34A;
  pointer-events: none;
  transform: translate(-30px, -40px);
  z-index: 50;
  border-radius: .3em;
  
  span {
    padding: 0 1em;
    width: 100%;
    text-align: center;
  }
}

.no-pointer { pointer-events: none; }

.full-screen {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 1;
}

circle {
  cursor: move;
}